import request from '@/utils/request'

// 获取用户信息
export function getInfo() {
    return request({
        url: 'web/info',
        method: 'get'
    })
}

// 获取账号安全等级
export function getLevel()
{
    return request({
        url: 'web/getLevel',
        method: 'get'
    })
}

// 设置支付密码
export function saveTpwd(data)
{
    return request({
        url: 'web/saveTpwd',
        method: 'post',
        data
    }) 
}

// 更新密码
export function updatePassword(data) {
    return request({
        url: 'web/updatePassword',
        method: 'post',
        data
    }) 
}

// 身份认证
export function userIdCard(data)
{
    return request({
        url: 'web/userIdCard',
        method: 'post',
        data
    })   
}

// 收款设置
export function updateInfo(data)
{
    return request({
        url: 'web/updateInfo',
        method: 'post',
        data
    })    
} 

export function getSetting()
{
    return request({
        url: 'web/getSetting',
        method: 'get'
    })     
}

export function idcardinfo()
{
    return request({
        url: 'web/idcardinfo',
        method: 'get'
    })     
}

export function getDealList(data)
{
    return request({
        url: 'web/getDealList',
        method: 'get',
        params: data
    })   
}



